<!-- // index.vue -->
<template>
    <div >
        <TheHeader :ins="1"></TheHeader>
        <div class="main-center">
          <div class="task-card-main">
            <div class=" task-card-box" v-for="(item,index) in dataList" :key="index">
                <TaskCard :idList="item" :roleCode="roleCode" :taskCount="taskCount"></TaskCard>
            </div>
          </div>
        </div>
    </div>
    
</template>
<script>
import TheHeader from "./../../components/TheHeader"
import { selIndexList } from "./../../api/index";
import TaskCard from "./../../components/taskCard"
export default {
  data() {
    return {
        dataList:{},
        roleCode:0,
        taskCount:''
    };
  },
  components:{
    TheHeader,
    TaskCard
  },
  created(){
    this.getDataList()
    // this.sss()
  },
  computed: {},
  mounted() {},
  methods: {
    getDataList(){
        selIndexList().then((res) => {
          console.log(res);
          if (res.code == 200) {
            this.dataList=res.data
            this.roleCode=res.role_code
            this.taskCount=res.task_count
          } else {
            this.notify(res.msg?res.msg:"登录失败", "error");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // sss(){
    //   testabaqus().then((res) => {})
    // }
  },
};
</script>
<style lang="less" scoped>
.task-card-main{
  padding: 60px 0;
}
.task-card-box{
    padding-bottom: 30px;
}
</style>
