<template>
  <div class="taskCard-box">
    <div class="taskCard-top-box">
      <div class="taskCard-id">任务代号：{{ idList.custom_id }}</div>
      <div class="taskCard-btn-list" v-if="roleCode == '超级管理' || roleCode == 'for'">
        <el-button type="warning" @click="clickFisrt(idList.id)">优先</el-button>
        <el-button type="info" @click="clickPause(idList.id)">暂停</el-button>
        <el-button type="success" @click="clickStart(idList.id)">启动</el-button>
        <el-button type="primary" @click="clickGet(idList.id)">领取</el-button>
        <el-button type="danger" @click="clickEnd(idList.id)">结束</el-button>
      </div>
    </div>
    <div class="taskCard-content-box">
      <div class="taskCard-cont-text">{{ dataList[ins]?.content }}</div>
      <div class="taskCard-cont-btn">
        <el-button type="primary" @click="clickJournal(idList.id,dataList[ins].item_id)">期刊</el-button>
        <el-button type="primary" @click="clickBook(idList.id,dataList[ins].item_id)">书籍</el-button>
        <el-button type="primary" @click="clickConfer(idList.id,dataList[ins].item_id)">会议</el-button>
        <el-button type="primary" @click="clickThesis(idList.id,dataList[ins].item_id)">论文</el-button>
        <el-button type="primary" @click="toRegister(idList.id,dataList[ins].item_id)">网页</el-button>
      </div>
    </div>
    <div class="taskCard-bottom-box">
        <el-button type="info" @click="pagePrev">上一页</el-button>
        <div class="taskCard-bottom-page">
            <p class="taskCard-ins">{{ ins+1 }}</p>
            <p>/{{ dataList.length }}</p>
        </div>
        <el-button type="info" @click="pageNext">下一页</el-button>
    </div>
  </div>
</template>
<script>
import { selIndexData,btnFirst,btnPause,btnStart,btnGet,btnEnd,btnJournal,btnThesis,btnBook,btnConfer,btnpages } from "./../api/index";
export default {
  data() {
    return {
      ins: 0,
      dataList:[],
      taskAll:'',
      taskResidue:''
    };
  },
  props: {
    idList: {
      type: Object,
    },
    roleCode: {
      type: String,
    },
    taskCount: {
      type: [Number || String],
    },
  },
  computed: {},
  mounted() {},
  created(){
    this.selData()
  },
  methods: {
    selData(){
      selIndexData(this.idList.id).then((res) => {
          if (res.code == 200) {
            this.dataList=res.data
            this.taskAll=res.task_all
            this.taskResidue=res.task_residue
          } else {
            this.notify(res.msg?res.msg:"操作失败", "error");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 优先按钮点击事件
    clickFisrt(id){
      const that=this
      btnFirst(id).then((res) => {
          if (res.code == 200) {
            this.$message({
              message: res.msg?res.msg:'设置成功',
              type: 'success'
            });
            setTimeout(function(){
              that.$router.go(0)
            },500)
          } else {
            this.$message({
              message: res.msg?res.msg:'设置失败',
              type: 'error'
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 暂停按钮点击事件
    clickPause(id){
      const that=this
      btnPause(id).then((res) => {
          if (res.code == 200) {
            this.$message({
              message: res.msg?res.msg:'设置成功',
              type: 'success'
            });
            setTimeout(function(){
              that.$router.go(0)
            },500)
          } else {
            this.$message({
              message: res.msg?res.msg:'设置失败',
              type: 'error'
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 开始按钮点击事件
    clickStart(id){
      const that=this
      btnStart(id).then((res) => {
          if (res.code == 200) {
            this.$message({
              message: res.msg?res.msg:'设置成功',
              type: 'success'
            });
            setTimeout(function(){
              that.$router.go(0)
            },500)
          } else {
            this.$message({
              message: res.msg?res.msg:'设置失败',
              type: 'error'
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 领取按钮点击事件
    clickGet(id){
      const that=this
      btnGet(id).then((res) => {
          if (res.code == 200) {
            window.localStorage.setItem("formatSets", JSON.stringify(res));
            this.$message({
              message: res.msg?res.msg:'设置成功',
              type: 'success'
            });
            setTimeout(function(){
              // that.$router.go(0)
              that.$router.push({path:`/format`,query:{id:res.data.task_id}})
            },500)
          } else {
            this.$message({
              message: res.msg?res.msg:'设置失败',
              type: 'error'
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 结束按钮点击事件
    clickEnd(id){
      const that=this
      btnEnd(id).then((res) => {
          if (res.code == 200) {
            this.$message({
              message: res.msg?res.msg:'设置成功',
              type: 'success'
            });
            setTimeout(function(){
              that.$router.go(0)
            },500)
          } else {
            this.$message({
              message: res.msg?res.msg:'设置失败',
              type: 'error'
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 期刊
    clickJournal(id,itemid){
      const that=this
      console.log(id,itemid);
      btnJournal(id).then((res) => {
          if (res.code == 200) {
              that.$router.push({path:`/api/journal`,query:{id,itemid}})
          } else {
            this.$message({
              message: res.msg?res.msg:'请求错误',
              type: 'error'
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 书籍
    clickBook(id,itemid){
      const that=this
      btnBook(id).then((res) => {
          if (res.code == 200) {
              that.$router.push({path:`/api/book`,query:{id,itemid}})
          } else {
            this.$message({
              message: res.msg?res.msg:'请求错误',
              type: 'error'
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 会议
    clickConfer(id,itemid){
      const that=this
      btnConfer(id).then((res) => {
          if (res.code == 200) {
              that.$router.push({path:`/api/confer`,query:{id,itemid}})
          } else {
            this.$message({
              message: res.msg?res.msg:'请求错误',
              type: 'error'
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 论文
    clickThesis(id,itemid){
      const that=this
      btnThesis(id).then((res) => {
          if (res.code == 200) {
              that.$router.push({path:`/api/thesis`,query:{id,itemid}})
          } else {
            this.$message({
              message: res.msg?res.msg:'请求错误',
              type: 'error'
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    toRegister(id,itemid) {
      const that=this
      btnpages(id).then((res) => {
          if (res.code == 200) {
              that.$router.push({path:`/api/webpages`,query:{id,itemid}})
          } else {
            this.$message({
              message: res.msg?res.msg:'请求错误',
              type: 'error'
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    pagePrev(){
        if(this.ins>0){
            this.ins=this.ins-1
        }
    },
    pageNext(){
        if(this.ins<this.dataList.length-1){
            this.ins=this.ins+1
        }
    }
  },
};
</script>
